export default {
    maskCell(v) {
        v = v.replace(/\D+/g, ''); //Remove tudo o que no  dgito
        v = v.replace(/(\d{1,2})(\d)/, '($1)$2'); //Coloca parenteses nos primeiros 2 digitos
        v = v.replace(/(\d{1,5})(\d{1,4})$/, '$1-$2'); //Coloca um hfen entre o stimo e o oitavo dgitos
        if (v.length == 14) v = v.replace(/(\d{2})(\d{5})(\d{1,4})$/, '($1)$2-$3');    
        if (v.length > 14) v = v.substr(0, 14);
        return v;
    },
    
    maskCPF(v) {
        v = v.replace(/\D+/g, ''); //Remove tudo o que no  dgito
        v = v.replace(/(\d{3})(\d)/, '$1.$2'); //Coloca um ponto entre o terceiro e o quarto dgitos
        v = v.replace(/(\d{3})(\d)/, '$1.$2'); //Coloca um ponto entre o terceiro e o quarto dgitos
        //de novo (para o segundo bloco de nmeros)
        v = v.replace(/(\d{3})(\d{1,2})$/, '$1-$2'); //Coloca um hfen entre o terceiro e o quarto dgitos
        if (v.length > 14) v = v.substr(0, 14);
        return v;
      }
}